"use client";

import { useState, useRef } from "react";
import { searchTracks } from "../lib/jamendo";
import { DoublyLinkedList, Track } from "../lib/playlist";

export default function Home() {
  const [query, setQuery] = useState("");
  const [playlist] = useState(new DoublyLinkedList());
  const [currentTrack, setCurrentTrack] = useState<Track | null>(null);
  const [loop, setLoop] = useState(false);
  const audioRef = useRef<HTMLAudioElement | null>(null);

  async function handleSearch(e: React.FormEvent) {
    e.preventDefault();
    const results = await searchTracks(query);
    results.forEach((track: Track) => playlist.add(track));
    const first = playlist.playCurrent();
    if (first) {
      setCurrentTrack(first);
    }
  }

  function playTrack(track: Track) {
    setCurrentTrack(track);
    if (audioRef.current) {
      audioRef.current.src = track.audio;
      audioRef.current.play();
    }
  }

  function handleNext() {
    const next = playlist.nextTrack();
    if (next) playTrack(next);
  }

  function handlePrev() {
    const prev = playlist.prevTrack();
    if (prev) playTrack(prev);
  }

  function handleFileUpload(e: React.ChangeEvent<HTMLInputElement>) {
    const file = e.target.files?.[0];
    if (file) {
      const url = URL.createObjectURL(file);
      const localTrack: Track = {
        id: "local",
        name: file.name,
        artist_name: "Local File",
        audio: url,
      };
      playlist.add(localTrack);
      playTrack(localTrack);
    }
  }

  return (
    <div style={{ padding: "2rem", maxWidth: "600px", margin: "auto" }}>
      <h1>üéµ Tone Static</h1>

      {/* Buscador */}
      <form onSubmit={handleSearch} style={{ marginBottom: "1rem" }}>
        <input
          type="text"
          placeholder="Buscar en Jamendo..."
          value={query}
          onChange={(e) => setQuery(e.target.value)}
          style={{ width: "70%", padding: "0.5rem" }}
        />
        <button type="submit" style={{ marginLeft: "0.5rem" }}>
          Buscar
        </button>
      </form>

      {/* Reproductor */}
      <div style={{ marginTop: "2rem" }}>
        {currentTrack && (
          <div>
            <h2>{currentTrack.name}</h2>
            <p>{currentTrack.artist_name}</p>
          </div>
        )}

        <audio ref={audioRef} controls loop={loop} style={{ width: "100%" }} />

        <div style={{ marginTop: "0.5rem" }}>
          <button onClick={() => audioRef.current?.pause()}>‚è∏Ô∏è Pausar</button>
          <button onClick={() => audioRef.current?.play()}>‚ñ∂Ô∏è Reanudar</button>
          <button onClick={handlePrev}>‚èÆÔ∏è Anterior</button>
          <button onClick={handleNext}>‚è≠Ô∏è Siguiente</button>
          <button onClick={() => setLoop(!loop)}>
            {loop ? "üîÅ Quitar Bucle" : "üîÅ Activar Bucle"}
          </button>
        </div>
      </div>

      {/* Subir archivo local */}
      <div style={{ marginTop: "2rem" }}>
        <h3>üìÇ Reproducir desde tu dispositivo</h3>
        <input type="file" accept="audio/*" onChange={handleFileUpload} />
      </div>
    </div>
  );
}
